@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');

:root {
  --color-text: #3f3f3f;
  --color-bg: #e0e0e0;
  --color-bg-hover: #aeaeae; 
  --color-bg-transparent: #aeaeae33; 
  --color-border: var(--color-text);
}

/* 
 * *** Default Button ***
 */

.btn {
  /* Layout */
  align-items: center;
  display: flex;
  padding: .5rem 1rem;

  /* Typography */
  color: var(--color-text); 
  font-family: 'Noto Sans', sans-serif;
  font-size: 0.875rem;
  line-height: 1.5;

  /* Skin */
  background-color: var(--color-bg);
  border: none;
  border-radius: 6px;
  box-shadow: 0px 2px 3px rgba(51, 51, 51, 0.2);
  cursor: pointer;
  fill: var(--color-text);
}

/* State skins */
.btn:active,
.btn:hover {
  background-color: var(--color-bg-hover);
}

.btn:focus {
  border: 1.5px solid #000; 
  outline: none;
}

.btn:disabled {
  background-color: #e0e0e0;
  border: none;
  box-shadow: none;
  color: #9e9e9e;
  cursor: default;
  fill: #9e9e9e;
}

.btn--disableshadow {
  box-shadow: none;
}

/*
 * *** Outline Button ***
 */

 .btn--outline {
  background-color: transparent;
  border: 1px solid var(--color-border);
  box-shadow: none;
 }

 .btn--outline:active,
 .btn--outline:hover {
  background-color: var(--color-bg-transparent);
}

/*
 * *** Text Button ***
 */

 .btn--text {
  background-color: transparent;
  border: none;
  box-shadow: none;
 }

 .btn--text:active,
 .btn--text:hover {
  background-color: var(--color-bg-transparent);
}

/*
 * *** Button Sizes ***
 */

 .btn--sm {
   padding: .375rem 0.75rem;
 }

 .btn--md {
   padding: .5rem 1rem;
 }

 .btn--lg {
   padding: .6875rem 1.375rem;
 }

 /*
  * *** Button Colors ***
  */

  .btn--primary {
    --color-text: #fff;
    --color-bg: #2962ff;
    --color-bg-hover: #0039cb; 
    --color-bg-transparent: #2962ff33; 
    --color-border: var(--color-bg);
  }

  .btn--outline.btn--primary,
  .btn--text.btn--primary {
    --color-text: #2962ff;
  }

  .btn--secondary {
    --color-text: #fff;
    --color-bg: #455a64;
    --color-bg-hover: #1c313a; 
    --color-bg-transparent: #455a6433; 
    --color-border: var(--color-bg);
  }

  .btn--outline.btn--secondary,
  .btn--text.btn--secondary {
    --color-text: #455a64;
  }

  .btn--danger {
    --color-text: #fff;
    --color-bg: #d32f2f;
    --color-bg-hover: #9a0007; 
    --color-bg-transparent: #d32f2f33; 
    --color-border: var(--color-bg);
  }

  .btn--outline.btn--danger,
  .btn--text.btn--danger {
    --color-text: #d32f2f;
  }
